.header {
	&__wrapper {
		padding-top: 35px;
		padding-bottom: 34px;
		position: relative;
		@include clr;
		@include r(991) {
			padding: 0; } }
	&__logo {
		height: 40px;
		margin-top: 20px;
		width: 281px;
		//+css-lock(200, 281, 1200, 1600, "width")
		@include cover;
		background-size: contain;
		display: block;
		float: left;
		@include r(991) {
			margin-top: 32px;
			margin-left: 13px; }
		@include r(767) {
			margin-left: 0; }
		@include r(560) {
			width: 130px;
			height: 30px;
			margin-top: 15px; } }
	&__menu {
		float: left;
		margin-top: 35px;
		@include css-lock(0, 99, 1300, 1600, "margin-left");
		@include r(1299) {
			margin-top: 23px;
			@include css-lock(450, 750, 992, 1300, "width"); }
		@include r(991) {
			display: none; } }
	&__menu-item {
		display: inline-block;
		margin-bottom: 10px;
		@include css-lock(5, 15, 1300, 1600, "margin-right");
		@include css-lock(5, 15, 1300, 1600, "margin-left"); }
	&__menu-link {
		text-transform: uppercase;
		color: $black;
		font-size: 15px;
		height: 1.3em;
		font-family: $PR;
		@include rmin(1200) {
			position: relative;
			transition: all .3s;
			&:before {
				content: '';
				position: absolute;
				bottom: -12px;
				height: 1px;
				width: 0px;
				background: $pink;
				transition: all .3s; }
			&:hover {
				color: $pink;
				&:before {
					width: 52px; } } } }
	&__menu-link--pink {
		color: $pink;
		@include rmin(1200) {
			&:hover {
				&:before {
					width: 100%; } } } } }
.basket-link {
	float: right;
	width: 210px;
	height: 99px;
	background-color: #e7e7e7;
	position: relative;
	z-index: 1;
	padding-top: 11px;
	text-align: right;
	&:after {
		z-index: 2;
		position: absolute;
		content: '';
		left: -27px;
		top: 0;
		border-style: solid;
		border-color: transparent #e7e7e7;
		border-width: 0px 28px 99px 0px; }
	@include r(991) {
		right: -19px; }
	@include r(767) {
		right: -25px; }
	@include r(560) {
		width: 135px;
		height: 60px;
		&:after {
			border-width: 0px 15px 60px 0px;
			left: -14px; } }
	&__text {
		margin-top: 20px;
		margin-right: 7px;
		display: inline-block;
		vertical-align: top;
		@include r(560) {
			margin-top: 5px; } }
	&__title {
		color: #242424;
		font-size: 21px;
		line-height: 1em;
		font-family: $PR;
		text-transform: uppercase;
		@include r(560) {
			font-size: 14px; } }
	&__count {
		color: #858484;
		font-size: 15px;
		line-height: 1em;
		font-family: $PR;
		@include r(560) {
			font-size: 12px; } }
	&__img {
		display: inline-block;
		width: 74px;
		height: 74px;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-color: #fff;
		border-radius: 50%;
		margin-right: 14px;
		background-size: 40px 40px;
		@include r(560) {
			width: 40px;
			height: 40px;
			margin-right: 5px;
			background-size: 30px 30px; } } }
