.mobnav-main {
	margin-top: 5px;
	&__item {
		border-bottom: 1px solid #242424;
		padding: 21px 0;
		&:last-of-type {
			position: relative;
			border-color: #fff;
			&:before {
				content: '';
				position: absolute;
				top: -1px;
				left: 0;
				width: 100%;
				height: 1px;
				background: $pink; } }
		@include r(560) {
			padding: 10px 0; } }
	&__link {
		color: $white;
		font-size: 26px;
		font-family: $PR;
		text-transform: uppercase;
		@include css-lock(16, 26, 560, 639); }
	&__link--pink {
		color: $pink; } }
.mobnav-second {
	margin-bottom: 46px;
	@include r(560) {
		margin-bottom: 20px; }
	&__item {
		padding: 16px 0;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			bottom: 0px;
			left: 50%;
			transform: translateX(-50%);
			width: 66%;
			height: 1px;
			background: #242424; }
		@include r(560) {
			padding: 10px 0; } }
	&__link {
		color: $gray;
		font-size: 18px;
		font-family: $PR;
		@include css-lock(14, 18, 560, 639); } }
.nav-responsive {
	display: none;
	@include r(991) {
		overflow: visible;
		text-align: center;
		width: 100%;
		padding-bottom: 45px;
		background-color: #2d2d2d;
		//display: block
		position: absolute;
		top: -1px;
		z-index: 2; }
	&__phone {
		display: inline-block;
		margin-bottom: 9px;
		font-size: 32px;
		@include css-lock(20, 32, 560, 639); }
	&__callback {
		@include css-lock(14, 18, 560, 639); } }
