* {
	box-sizing: border-box; }

body {
  background: #FFF; }
a {
 	text-decoration: none; }
.h1 {
	color: $pink;
	display: inline-block;
	font-family: $PS;
	font-size: 26px;
	text-transform: uppercase;
	@include r(639) {
		font-size: 22px; } }
.h1--hidden {
	@include hidetext; }
.h1--big {
	font-size: 36px;
	color: $black;
	font-family: $PEB; }

.h2 {
	font-family: $PS;
	color: $gray-darnest;
	font-size: 30px;
	line-height: 1em;
	font-weight: normal; }
.h2--small {
	font-size: 19px; }
.h3 {
	text-align: center;
	color: $pink;
	text-transform: uppercase;
	font-size: 45px;
	line-height: 1em;
	font-family: $PT;
	font-weight: normal;
	@include r(560) {
		font-size: 28px;
		font-weight: normal; } }
.h3--negative-indent {
	display: inline-block;
	position: relative;
	top: -24px;
	background: #fff;
	padding: 0 32px;
	@include r(767) {
		top: -12px; } }
.h4 {
	color: $gray-darnest;
	font-size: 30px;
	line-height: 1em;
	font-family: $PBK;
	text-transform: uppercase;
	font-weight: normal; }
.grayLinkStyle {
	color: $gray;
	font-size: 15px;
	line-height: 1em;
	font-family: $PR; }
.mainMenuStile {
	color: $black;
	font-size: 15px;
	line-height: 1.3em;
	font-family: $PR; }
.three-col-list {
	text-align: center;
	font-size: 0; }
.three-col-list__item {
	vertical-align: top;
	display: inline-block;
	@include css-lock(300, 484, 992, 1599, "width");
	&:nth-of-type(3n+2) {
		margin-left: 60px;
		margin-right: 60px;
		@include r(1599) {
			margin-left: 30px;
			margin-right: 30px; } }
	@include r(991) {
		width: 484px;
		margin: 0 0 34px 0!important;
		&:last-of-type {
			margin-bottom: 0!important; } }
	@include r(560) {
		width: 100%; } }
.body-ovh {
	overflow: hidden; }
.js-hidden {
	transition: opacity .4s; }
.is-hidden {
	opacity: 0; }
.close-default {
	width: 22px;
	height: 22px;
	position: absolute;
	transition: all .3s;
	background-color: transparent;
	font-size: 18px;
	font-family: $PR;
	line-height: 22px;
	text-align: center;
	cursor: pointer;
	color: #eaeaea;
	&:hover {
		background-color: #f3f3f3;
		color: $white; } }
.btn {
	padding: 24px;
	background: $pink;
	border: 1px solid $pink;
	font-family: $PEB;
	font-size: 22px;
	text-transform: uppercase;
	color: $white;
	text-align: center;
	display: block;
	@include rmin(1200) {
		transition: all .3s;
		&:hover {
			background: transparent;
			color: $pink; } }
	&--small {
		padding: 15px 15px; } }
.btn-border {
	display: inline-block;
	color: $pink;
	font-family: $PS;
	font-size: 15px;
	padding: 8px 12px;
	border: 1px solid $pink;
	text-align: center;
	transition: all .3s;
	&:hover {
		opacity: 0.8; } }

.error_form {
	box-shadow: inset 0 0 5px red;
	border-color: red; }
.valid_form {
	box-shadow: inset 0 0 5px green;
	border-color: green; }
