//.basket-page
//	.header__wrapper
//		padding-bottom: 0
.basket {
	//font-size: 0
	padding-top: 13px;
	@include css-lock(67, 126, 991, 1599, "margin-bottom");
	@include r(991) {
		padding-top: 55px; }
	@include r(767) {
		padding-top: 44px; }
	&__container {
		@include clr; }
	&__left {
		@include css-lock(600, 722, 1300, 1599, "width");
		float: left;
		@include r(1300) {
			width: 80%;
			float: none;
			margin: 0 auto; }
		@include r(991) {
			width: 100%;
			padding-left: 9px; } }
	&__right {
		@include css-lock(650, 829, 1300, 1599, "width");
		float: right;
		@include css-lock(25, 53, 1300, 1599, "padding-left");
		@include css-lock(25, 53, 1300, 1599, "padding-right");
		padding-top: 46px;
		padding-bottom: 47px;
		background-color: #f3f3f3;
		margin-top: 6px;
		@include r(1300) {
			width: 80%;
			float: none;
			margin: 0 auto; }
		@include r(991) {
			width: 100%;
			margin-top: 55px;
			padding-left: 7px;
			padding-right: 7px; }
		@include r(639) {
			margin-top: 30px;
			padding: 15px 5px; }
		@include clr; }
	&__total-count {
		margin-top: 30px;
		padding: 20px 0;
		color: $gray-semi-light;
		font-size: 20px;
		line-height: 1em;
		font-family: $PR;
		border-top: 1px solid  #f3f3f3;
		border-bottom: 1px solid  #f3f3f3;
		@include r(639) {
			font-size: 17px; } } }
.bproduct-list {
	margin-top: 33px;
	margin-bottom: 126px;
	@include r(639) {
		margin-bottom: 60px; } }
.bproduct {
	position: relative;
	font-size: 0;
	border-top: 1px solid  #f3f3f3;
	border-bottom: 1px solid  #f3f3f3;
	margin-bottom: 19px;
	&__img {
		width: 110px;
		height: 110px;
		@include cover;
		display: inline-block;
		margin-right: 24px;
		@include r(639) {
			margin: 10px auto;
			display: block; } }
	&__desc {
		display: inline-block;
		vertical-align: top;
		padding: 10px 0 0;
		width: calc(100% - 134px);
		@include r(639) {
			width: 100%;
			text-align: center; } }
	&__h2 {
		display: inline-block;
		color: $gray-middle;
		line-height: 1.7em;
		letter-spacing: -0.4px; }
	&__h2link {
		display: inline-block; }
	&__option, &__count {
		font-size: 16px;
		font-family: $PR;
		color: $gray;
		line-height: 1em; }
	&__option {
		position: relative;
		top: -7px;
		letter-spacing: -0.4px; }
	&__inner {
		border-top: 1px solid  #f3f3f3;
		//min-height: 45px
		@include vertical;
		text-align: left;
		margin-top: 6px;
		//line-height: 45px
		@include r(639) {
			text-align: center;
			min-height: auto;
			padding: 10px 0; } }
	&__count, &__sum {
		display: inline-block;
		vertical-align: middle; }
	&__sum {
		color: $black;
		font-family: $PS;
		font-size: 20px;
		padding-right: 10px;
		border-right: 1px solid  #f3f3f3;
		min-height: 45px;
		padding-top: 9px;
		@include r(639) {
			padding-right: 0;
			border: none;
			display: block;
			padding-top: 0; } }
	&__count {
		padding-left: 14px;
		display: inline-block;
		vertical-align: middle;
		font-size: 0;
		@include r(639) {
			padding-left: 0;
			display: block; } }
	&__count-title, &__count-value {
		vertical-align: top;
		display: inline-block;
		border-top: 1px solid  #d4d4d4;
		border-bottom: 1px solid  #d4d4d4;
		height: 20px;
		line-height: 20px;
		color: #636363;
		width: 38px;
		font-size: 16px;
		text-align: center; }
	&__count-title {
		border: none;
		margin-right: 7px;
		width: auto;
		text-align: left; }
	&__minus, &__plus {
		display: inline-block;
		width: 20px;
		height: 20px;
		border: 1px solid #d4d4d4;
		position: relative;
		cursor: pointer;
		transition: all .2s;
		@include nosel;
		&:hover {
			background: #d4d4d4 + 20%; }
		&:before {
			@include vcenter;
			content: ''; } }
	&__minus {
		&:before {
			@include arr(5, 9, #bababa, l); } }
	&__plus {
		&:before {
			@include arr(5, 9, #bababa, r); } }
	&__close {
		right: 5px;
		top: 5px; } }
.btotal-sum {
	border-top: 1px solid  #c7c7c7;
	border-bottom: 1px solid $pink;
	padding: 15px 0 15px;
	font-size: 0;
	&__left, &__right {
		display: inline-block;
		width: 50%; }
	&__left {
		color: $gray1;
		font-family: $PR;
		font-size: 20px;
		line-height: 1em; }
	&__right {
		text-align: right;
		color: $pink;
		font-size: 24px;
		line-height: 1em;
		font-family: $PR; }
	&__value {
		font-family: $PB;
		font-weight: 700; } }
.delivery {
	@include css-lock(280, 357, 1300, 1599, "width");
	float: left;
	@include r(1300) {
		width: 357px; }
	@include r(767) {
		width: 259px; }
	@include r(639) {
		width: 100%; }
	&__input, &__textarea {
		width: 100%;
		color: $gray;
		font-family: $PR;
		font-size: 20px;
		line-height: 1em;
		border: 1px solid #c7c7c7;
		padding: 15px 15px;
		@include r(639) {
			padding: 10px;
			font-size: 16px; } }
	&__input {
		margin-bottom: 8px; }
	&__textarea {
		resize: none;
		min-height: 178px;
		width: 100%; } }
.delivery__title, .payment__title {
	color: $gray;
	font-size: 30px;
	text-transform: uppercase;
	font-family: $PEB;
	margin-bottom: 28px;
	line-height: 1em;
	@include r(639) {
		font-size: 20px;
		margin-bottom: 15px; } }
.payment {
	float: right;
	width: 310px;
	@include r(767) {
		width: 293px; }
	@include r(639) {
		width: 100%;
		margin-top: 20px; }
	&__input {
		display: none;
		&:checked+label .payment__icon {
			&:before {
				width: 15px;
				height: 15px; } } }
	&__label {
		color: $black-light;
		font-family: $PR;
		font-size: 20px;
		vertical-align: top;
		padding-left: 52px;
		margin-bottom: 19px;
		padding-top: 12px;
		display: block;
		cursor: pointer;
		line-height: 1em; }
	&__desc {
		font-size: 15px;
		color: $gray2;
		display: block; }
	&__inner {
		position: relative; }
	&__icon {
		left: 0;
		top: 2px;
		width: 35px;
		height: 35px;
		background-color: white;
		border: 1px solid #c7c7c7;
		border-radius: 50%;
		position: absolute;
		display: inline-block;
		&:before {
			transition: all .3s;
			content: '';
			@include vcenter;
			border-radius: 50%;
			width: 0;
			height: 0;
			background: $pink; } }
	&__btn {
		width: 310px;
		margin-top: 161px;
		@include r(991) {
			width: 100%; }
		@include r(639) {
			margin-top: 50px; } } }
