.cfilter {
	width: 280px;
	float: left;
	@include r(991) {
		display: none; }
	.noUi-horizontal {
		.noUi-handle {
			@include rmin(992) {
				left: -2px; } } }
	.noUi-base {
		@include rmin(992) {
			width: 91%; } }
	&__wrapper {
		display: none;
		font-size: 0; }
	&__option {
		padding-bottom: 37px;
		padding-top: 26px;
		border-top: 1px solid #dfdfdf;
		&:first-of-type {
			border-top: none; } }
	&__title {
		cursor: pointer;
		display: inline-block;
		color: #9f9f9f;
		margin-bottom: 9px;
		position: relative;
		font-family: $PS;
		font-size: 20px;
		line-height: 1em;
		&:after {
			content: '';
			position: absolute;
			right: -22px;
			top: 50%;
			margin-top: -3px;
			@include arr(14, 7, #9ea2a3, b);
			transition: all .3s; } }
	&__title.is-open {
		&:after {
			transform: rotateX(180deg); } }
	&__title--without-arrow {
		cursor: default;
		margin-bottom: 16px;
		&:after {
			display: none; } }
	&__btn {
		font-family: $PS;
		width: 100%;
		@include r(991) {
			margin-top: 45px;
			width: 220px;
			margin-left: auto;
			margin-right: auto; } }
	&__price {
		padding-bottom: 0; }
	&__price-wrapper {
		font-size: 0;
		margin-bottom: 31px;
		@include clr; }
	&__price-input {
		padding: 7px 8px;
		background-color: #f3f3f3;
		border: 1px solid #e6e6e6;
		width: 128px;
		display: block;
		float: left;
		color: $gray;
		font-family: $PR;
		font-size: 20px;
		@include placeholder-color($gray);
		&:last-of-type {
			float: right; } }
	&__slider {
		margin-bottom: 20px; }
	&__price-min, &__price-max {
		width: 50%;
		font-family: $PS;
		font-size: 16px;
		color: $gray;
		display: inline-block; }
	&__price-max {
		text-align: right; } }
.check-block {
	&__input {
		display: none;
		&:checked + label >.check-block__icon {
			&:before {
				background: $pink; } } }
	&__text {
		@include nosel;
		display: inline-block;
		color: $black;
		font-family: $PR;
		font-size: 18px;
		line-height: 1.4em;
		cursor: pointer; }
	&__icon {
		margin-right: 9px;
		display: inline-block;
		cursor: pointer;
		width: 25px;
		height: 25px;
		background-color: #f3f3f3;
		border: 1px solid #e6e6e6;
		position: relative;
		top: 5px;
		&:before {
			content: '';
			transition: all .2s;
			@include vcenter;
			width: 13px;
			height: 13px;
			background: transparent; }
		&:hover {
			&:before {
				background-color: #e6e6e6; } } } }
.cfilter-dropbox {
	display: none;
	position: absolute;
	width: 100%;
	z-index: 2;
	background: #fff;
	left: 0; }
