.banner {
	display: inline-block;
	position: relative;
	text-align: left;
	@include css-lock(300, 484, 992, 1599, "width");
	vertical-align: top;
	&:nth-of-type(3n+2) {
		margin-left: 60px;
		margin-right: 60px;
		@include r(1599) {
			margin-left: 30px;
			margin-right: 30px; } }
	@include r(991) {
		width: 484px;
		margin: 0px 5px!important; }
	@include r(639) {
		margin: 0!important; }
	&__link {
		display: block;
		height: 190px;
		padding: 22px 27px;
		@include cover;
		@include r(560) {
			height: 150px; } }
	&__title {
		color: $white;
		line-height: 1em;
		font-family: $PBK;
		text-transform: uppercase;
		display: inline-block;
		@include css-lock(30, 55, 992, 1599);
		@include css-lock(40, 0, 992, 1599, "margin-top");
		@include r(991) {
			font-size: 55px;
			margin-top: 0; }
		@include r(560) {
			font-size: 30px; } }
	&__title--large {
		font-size: 70px;
		line-height: 0.9em;
		@include css-lock(30, 70, 992, 1599);
		@include r(991) {
			font-size: 70px; }
		@include r(560) {
			margin-top: 20px; } }
	&__price {
		position: absolute;
		right: 0;
		bottom: 29px;
		background: rgba(255,255,255, 0.8);
		color: $pink;
		font-family: $PBK;
		line-height: 1em;
		text-transform: uppercase;
		@include css-lock(130, 227, 992, 1599, "width");
		@include css-lock(60, 78, 992, 1599, "height");
		@include css-lock(18, 25, 992, 1599, "padding-top");
		@include css-lock(5, 18, 992, 1599, "padding-left");
		@include css-lock(25, 35, 992, 1599);
		&:after {
			z-index: 1;
			position: absolute;
			content: '';
			left: -29px;
			top: 0;
			border-style: solid;
			border-color: transparent rgba(255,255,255, 0.8);
			border-right-width: 29px;
			border-top-width: 0px;
			border-left-width: 0;
			@include css-lock(60, 78, 992, 1599, "border-bottom-width");
			@include r(991) {
				border-bottom-width: 78px; }
			@include r(560) {
				border-bottom-width: 60px; } }
		@include r(991) {
			width: 227px;
			height: 78px;
			padding-left: 16px;
			font-size: 35px;
			padding-top: 24px; }
		@include r(560) {
			width: 130px;
			height: 60px;
			padding-top: 16px;
			padding-left: 0px;
			font-size: 25px;
			bottom: 8px; }
		span {
			font: 300 35px/1em $PR;
			text-transform: none;
			@include css-lock(22, 35, 992, 1599);
			@include r(991) {
				font-size: 35px; }
			@include r(560) {
				font-size: 22px; } } }
	&__date {
		color: $white;
		font-family: $PT;
		line-height: 1em;
		text-transform: uppercase;
		display: block;
		margin-top: 12px;
		@include css-lock(25, 45, 992, 1599);
		@include r(991) {
			font-size: 45px; }
		@include r(560) {
			font-size: 25px; } } }
