.pagin {
	margin: 47px 0 112px;
	@include r(991) {
			margin: 44px 0 140px; }
	@include r(767) {
		margin: 30px 0 70px; }
	@include r(639) {
		margin: 15px 0 50px; }
	&__list {
		text-align: center;
		font-size: 0;
		margin-right: 18px;
		position: relative;
		@include r(991) {
			margin-right: 0; } }
	&__item {
		display: inline-block;
		transition: all .3s;
		@include r(550) {
			&:nth-of-type(5),
			&:nth-of-type(6) {
				display: none; } }
		&:hover {
			background-color: #efefef; } }
	&__prev-item {
		margin-right: 60px;
		&:hover {
			background: none; }
		@include r(767) {
			margin-right: 5px; }
		@include r(550) {
			margin-right: 0; }
		//	position: absolute
		//	left: 0
 }		//	margin: 0
	&__next-item {
		margin-left: 60px;
		&:hover {
			background: none; }
		@include r(767) {
			margin-left: 5px; }
		@include r(639) {
			margin-left: 0; }
 }		//	position: absolute
	&__link {
		font-family: $PS;
		font-size: 26px;
		height: 56px;
		color: $gray;
		line-height: 56px;
		display: inline-block;
		vertical-align: middle;
		width: 56px;
		@include r(550) {
			width: 40px;
			height: 40px;
			line-height: 40px; } }
	&__link--active {
		color: $white;
		background-color: $pink; }
	&__prev-link, &__next-link {

		position: relative;

		&:before {
			content: '';
			@include vcenter; } }
	&__prev-link {

		&:before {
			@include arr(11, 23, #6b6b6b, l); } }
	&__next-link {
		&:before {
			@include arr(11, 23, #6b6b6b, r); } } }
